}
h2 {
  font-family: "Courier New", Courier, monospace;
  font-weight: 300;
}
.slick-prev {
  left: 2% !important;
  z-index: 1;
  background: none !important;
}
.slick-next {
  right: 2% !important;
  z-index: 1;
  background: none !important;
}
.slick-prev:before,
.slick-next:before {
  color: black !important;
}
.heading {
